import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {Grid, Paper, Button} from "@material-ui/core";
import {Formik, Form} from "formik";
import {helpers} from "../../../helper/helper";
import CreateZoleForm from "./CreateZoleForm";
import {useSnackbar} from "notistack";

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        padding: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
            marginTop: theme.spacing(2),
            marginBottom: theme.spacing(2),
            padding: theme.spacing(3),
        },
    },
    submit: {
        margin: theme.spacing(4, 0, 1, 0),
    },
    wrapper: {
        position: "relative",
    },
    buttonProgress: {
        position: "absolute",
        top: "50%",
        left: "91%",
    },
}));


const CreateZole = ({PlanData, editCheck, singlePlan, editPlan}) => {
    const classes = useStyles();
    const {enqueueSnackbar} = useSnackbar();

    const initialValues = {
        zole: "",
        price: "",
        bonus: "",
    };

    const editValues = {
        zole: singlePlan && singlePlan.zole ? singlePlan.zole : '',
        price: singlePlan && singlePlan.price ? singlePlan.price : '',
        bonus: singlePlan && singlePlan.bonus ? singlePlan.bonus : '',
    };

    const handleSubmit = async (values) => {
        const url = "zoles";
        const result = await helpers.apiCall("POST", url, values);
        if (result.status === 200) {
            PlanData()
            enqueueSnackbar("Zoles Created successfully", {
                variant: 'success',
                autoHideDuration: 3000,
            });
        }
        else {
            enqueueSnackbar(result.message, {
                variant: 'error',
                autoHideDuration: 3000,
            });
        }
    };

    return (
        <Paper className={classes.paper} square>
            <Formik
                enableReinitialize="true"
                initialValues={editCheck ? editValues : initialValues}
                onSubmit={editCheck ? editPlan : handleSubmit}
            >
                {(formik) => {
                    return (
                        <Form autoComplete="off">
                            <CreateZoleForm formik={formik}/>
                            <Grid align="right" item style={{marginTop: "10px"}}>
                                <div className={classes.wrapper}>
                                    <Button
                                        variant="contained"
                                        type="submit"
                                        className={classes.submit}
                                        color="primary"
                                    >
                                        {editCheck ? "Edit" : "Create"}
                                    </Button>
                                </div>
                            </Grid>
                        </Form>
                    );
                }}
            </Formik>
        </Paper>


    );
};

export default CreateZole;
