import React from "react";

import {Grid, TextField} from "@material-ui/core";

const CreateZoleForm = ({formik}) => {
    const {handleChange, values, touched, errors} = formik;

    return (
        <Grid container spacing={3}>
            <Grid sm={12} xs={12} item>
                <TextField
                    fullWidth
                    type="number"
                    variant="outlined"
                    id="zole"
                    name="zole"
                    label="Zole"
                    value={values.zole}
                    onChange={handleChange}
                    error={touched.zole && Boolean(errors.zole)}
                    helperText={touched.zole && errors.zole}
                />
            </Grid>

            <Grid sm={12} xs={12} item>
                <TextField
                    fullWidth
                    type="number"
                    variant="outlined"
                    id="price"
                    name="price"
                    label="Price"
                    value={values.price}
                    onChange={handleChange}
                    error={touched.price && Boolean(errors.price)}
                    helperText={touched.price && errors.price}
                />
            </Grid>

            <Grid sm={12} xs={12} item>
                <TextField
                    fullWidth
                    type="number"
                    variant="outlined"
                    id="bonus"
                    name="bonus"
                    label="Bonus"
                    value={values.bonus}
                    onChange={handleChange}
                    error={touched.bonus && Boolean(errors.bonus)}
                    helperText={touched.bonus && errors.bonus}
                />
            </Grid>
        </Grid>
    );
};

export default CreateZoleForm;
