import React, {useEffect, useState, useCallback} from "react";
import PageContainer from "../../layouts/PageContainer";
import CreatePlan from "./CreatePlan";
import {Grid} from "@material-ui/core";
import PlanList from "./PlanList";
import {helpers} from "../../helper/helper";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
import {useSnackbar} from "notistack";

const Plan = () => {
    const {enqueueSnackbar} = useSnackbar();
    const [planList, setPlanList] = useState();
    const [check, setCheck] = useState(true);
    const [singlePlan, setSinglePlan] = useState();
    const [planId, setPlanId] = useState();
    const [editCheck, setEditCheck] = useState(false);

    const PlanData = useCallback(async () => {
        const url = "plans";
        const result = await helpers.apiCall("GET", url);
        setPlanList(
            result && result.data && result.data.result ? result.data.result : []
        );
    }, [setPlanList]);

    useEffect(() => {
        PlanData();
    }, [PlanData]);

    const editPlan = async (values) => {
        const url = `plans/${planId}`;
        const result = await helpers.apiCall("PUT", url, values);
        if (result.status === 200) {
            PlanData();

            setEditCheck(false);
            enqueueSnackbar("Plan Updated successfully", {
                variant: 'success',
                autoHideDuration: 3000,
            });
        } else {
            enqueueSnackbar(result.message, {
                variant: 'error',
                autoHideDuration: 3000,
            });
        }
    };

    const singlePlanById = async (id) => {
        setPlanId(id);
        const url = `plans/${id}`;
        const result = await helpers.apiCall("get", url);
        if (result.status === 200) {
            setSinglePlan(result.data.result);
            setEditCheck(true);
        } else {
            setEditCheck(false);
        }
    };

    const deletePlan = async (id) => {
        const url = `plans/${id}`;
        const result = await helpers.apiCall("DELETE", url);
        if (result.status === 200) {
            PlanData();
            enqueueSnackbar("Plan Deleted successfully", {
                variant: 'success',
                autoHideDuration: 3000,
            });
        } else {

            enqueueSnackbar(result.message, {
                variant: 'error',
                autoHideDuration: 3000,
            });

        }
    };
    const handleChange = () => {
        setCheck(!check);
    };

    return (
        <PageContainer pageheading="CREATE PLAN">
            <Grid container spacing={3}>
                <Grid item sm={6} xs={12}>
                    <CreatePlan
                        PlanData={PlanData}
                        editCheck={editCheck}
                        singlePlan={singlePlan}
                        editPlan={editPlan}
                    />
                </Grid>
                <Grid item sm={6} xs={12}>
                    <FormGroup row>
                        <FormControlLabel
                            control={
                                <Switch color="primary" checked={check} onChange={handleChange} name="check"/>
                            }
                            label="Show Plan List"
                        />
                    </FormGroup>

                    <PlanList
                        planList={planList}
                        deletePlan={deletePlan}
                        editPlan={editPlan}
                        singlePlanById={singlePlanById}
                        check={check}
                    />
                </Grid>
            </Grid>
        </PageContainer>
    );
};

export default Plan;
