import React from "react";
import {IconButton, LinearProgress, Paper} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";
import DataTable from "react-data-table-component";
import ArrowDownward from "@material-ui/icons/ArrowDownward";

const sortIcon = <ArrowDownward/>;

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        padding: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
            marginTop: theme.spacing(2),
            marginBottom: theme.spacing(6),
            padding: theme.spacing(3),
        },
        table: {
            minWidth: 650,
        },
    },
}));

const TaskList = ({channelsList, editGifts, deleteGifts, check, singleGiftsById}) => {
    const classes = useStyles();


    const heading = [
        {
            name: "Title",
            selector: "title",
            wrap: true,
            sortable: true,
        }, {
            name: "Link",
            selector: "link",
            wrap: true,
            sortable: true,
        }, {
            name: "Reward",
            selector: "reward",
            wrap: true,
            sortable: true,
        }, {
            name: "Reward Type",
            selector: "rewardType",
            wrap: true,
            sortable: true,
        }, {
            name: "Image",
            selector: "bonus",
            wrap: true,
            sortable: true,

            cell: (channelsList) => (<div>
                <img style={{height: '60px', width: '60px'}} src={channelsList.image} alt="img"/>
            </div>)
        }, {
            name: "Action",
            wrap: true,
            center: true,

            cell: (channelsList) => (
                <div>
                    <IconButton
                        edge="end"
                        aria-label="edit"
                        onClick={() => {
                            singleGiftsById(channelsList._id);
                        }}
                    >
                        <EditIcon color="primary"/>
                    </IconButton>

                    <IconButton
                        edge="end"
                        aria-label="delete"
                        onClick={() => {
                            deleteGifts(channelsList._id)
                        }}
                    >
                        <DeleteIcon color="primary"/>
                    </IconButton>
                </div>
            )
        }
    ];

    return (
        <div>
            {!channelsList ? (
                <LinearProgress color="primary"/>
            ) : (
                <Paper className={classes.paper} square>
                    {check ?
                        <DataTable
                            columns={heading}
                            noHeader={true}
                            data={channelsList}
                            pagination
                            paginationRowsPerPageOptions={[10, 20, 30, 50, 100]}
                            sortIcon={sortIcon}
                            striped={true}
                            highlightOnHover={true}
                            responsive={true}
                        />
                        : ''}
                </Paper>
            )}
        </div>
    );
};

export default TaskList;
