import React, {useEffect, useState, useCallback} from "react";
import PageContainer from "../../layouts/PageContainer";
import CreateBanner from "./CreateBanner";
import {Grid} from "@material-ui/core";
import BannerList from "./BannerList";
import {helpers} from "../../helper/helper";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
import {useSnackbar} from "notistack";

const Banner = () => {
    const {enqueueSnackbar} = useSnackbar();
    const [giftsList, setGiftsList] = useState();
    const [check, setCheck] = useState(true);
    const [singleGifts, setSingleGifts] = useState();
    const [giftsId, setGiftsId] = useState();
    const [editCheck, setEditCheck] = useState(false);
    const [file, setFile] = useState();
    const [image, setImage] = useState([]);
    const [types, setTypes] = React.useState('');

    const GiftsData = useCallback(async () => {
        const url = "banners";
        const result = await helpers.apiCall("GET", url);
        setGiftsList(
            result && result.data && result.data.result ? result.data.result : []
        );
    }, [setGiftsList]);

    useEffect(() => {
        GiftsData();
    }, [GiftsData]);

    const editBanner = async (values, {resetForm}) => {
        let formData = new FormData();
        formData.append("title", values.title);
        formData.append("link", values.link);
        formData.append("image", image ? image[0] : singleGifts.image);
        formData.append("type", types ? types : null);
        const url = `gifts/${giftsId}`;
        const result = await helpers.apiCallForm("PUT", url, formData);
        if (result.status === 200) {
            GiftsData();
            setEditCheck(false);
            setSingleGifts();
            setImage([]);
            enqueueSnackbar("Banner updated successfully", {
                variant: 'success',
                autoHideDuration: 3000,
            });
        }
        else {
            enqueueSnackbar(result.message, {
                variant: 'error',
                autoHideDuration: 3000,
            });
        }
    };

    const singleGiftsById = async (id) => {
        setGiftsId(id);
        const url = `banners/${id}`;
        const result = await helpers.apiCall("get", url);
        if (result.status === 200) {
            setSingleGifts(result.data.result);
            setEditCheck(true);
        } else {
            setEditCheck(false);
        }
    };

    const deleteGifts = async (id) => {
        const url = `banners/${id}`;
        const result = await helpers.apiCall("DELETE", url);
        if (result.status === 200) {
            GiftsData();
            enqueueSnackbar("Banner Deleted successfully", {
                variant: 'success',
                autoHideDuration: 3000,
            });
        }
        else {
            enqueueSnackbar(result.message, {
                variant: 'error',
                autoHideDuration: 3000,
            });
        }
    };
    const handleChange = () => {
        setCheck(!check);
    };

    return (
        <PageContainer pageheading="BANNER">
            <Grid container spacing={3}>
                <Grid item sm={6} xs={12}>
                    <CreateBanner
                        GiftsData={GiftsData}
                        editCheck={editCheck}
                        singleGifts={singleGifts}
                        editBanner={editBanner}
                        setFile={setFile}
                        file={file}
                        setSingleGifts={setSingleGifts}
                        image={image}
                        setImage={setImage}
                        setTypes={setTypes}
                        types={types}
                    />
                </Grid>
                <Grid item sm={6} xs={12}>
                    <FormGroup row>
                        <FormControlLabel
                            control={
                                <Switch checked={check} onChange={handleChange} name="check" color="primary"/>
                            }
                            label="Show Banner List"
                        />
                    </FormGroup>

                    <BannerList
                        giftsList={giftsList}
                        deleteGifts={deleteGifts}
                        editBanner={editBanner}
                        singleGiftsById={singleGiftsById}
                        check={check}
                    />
                </Grid>
            </Grid>
        </PageContainer>
    );
};

export default Banner;
