import React, {useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {Grid, Paper, Button} from "@material-ui/core";
import {Formik, Form} from "formik";
import {helpers} from "../../../helper/helper";
import {ChannelValidation} from "../../../helper/ValidationSchema";
import CreateTaskForm from "./CreateTaskForm";
import {useSnackbar} from "notistack";

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        padding: theme.spacing(2),
        [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
            marginTop: theme.spacing(2),
            marginBottom: theme.spacing(2),
            padding: theme.spacing(3),
        },
    },
    submit: {
        margin: theme.spacing(4, 0, 1, 0),
    },
    wrapper: {
        position: "relative",
    },
    buttonProgress: {
        position: "absolute",
        top: "50%",
        left: "91%",
    },
}));

const CreateTask = ({ChannelsData, editCheck, singleGifts, editChannels, file, setFile, setSingleGifts, image, setImage, setRewardTypes, rewardTypes}) => {
    const {enqueueSnackbar} = useSnackbar();
    const classes = useStyles();
    const [img, setImg] = useState(false);
    const handleChanges = (event) => {
        setRewardTypes(event.target.value);
    };
    const initialValues = {
        title: "",
        link: "",
        image: "",
        reward: '',
        rewardType: ''
    };

    const editValues = {
        title: singleGifts && singleGifts.title ? singleGifts.title : "",
        link: singleGifts && singleGifts.link ? singleGifts.link : "",
        image: singleGifts && singleGifts.image ? singleGifts.image : "",
        reward: singleGifts && singleGifts.reward ? singleGifts.reward : "",
        rewardType: singleGifts && singleGifts.rewardType ? singleGifts.rewardType : "",
    };

    const handleSubmit = async (values, action) => {
        let formData = new FormData();
        formData.append("title", values.title);
        formData.append("link", values.link);
        formData.append("image", image ? image[0] : values.image);
        formData.append("reward", values.reward);
        formData.append("rewardType", rewardTypes ? rewardTypes : '');

        const url = "tasks";
        const result = await helpers.apiCallForm("POST", url, formData);
        if (result.status === 200) {
            ChannelsData();
            setRewardTypes('');
            setImg(true);
            enqueueSnackbar("Created successfully", {
                variant: 'success',
                autoHideDuration: 3000,
            });
        }
        else {
            enqueueSnackbar(result.message, {
                variant: 'error',
                autoHideDuration: 3000,
            });
        }
        setImg(false);
    };

    return (
        <Paper className={classes.paper} square>
            <Formik
                enableReinitialize="true"
                initialValues={editCheck ? editValues : initialValues}
                onSubmit={editCheck ? editChannels : handleSubmit}
                validationSchema={
                    ChannelValidation
                }
            >
                {(formik) => {
                    return (
                        <Form autoComplete="off">
                            <CreateTaskForm
                                formik={formik}
                                setFile={setFile}
                                file={file}
                                singleGifts={singleGifts}
                                setSingleGifts={setSingleGifts}
                                image={image}
                                setImage={setImage}
                                img={img}
                                handleChanges={handleChanges}
                                rewardTypes={rewardTypes}
                            />
                            <Grid align="right" item style={{marginTop: "10px"}}>
                                <div className={classes.wrapper}>
                                    <Button
                                        variant="contained"
                                        type="submit"
                                        className={classes.submit}
                                        color="primary"
                                    >
                                        {editCheck ? "Edit" : "Create"}
                                    </Button>
                                </div>
                            </Grid>
                        </Form>
                    );
                }}
            </Formik>
        </Paper>
    );
};

export default CreateTask;
